import React from 'react'
import './Header.css'
import SearchIcon from '@mui/icons-material/Search';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import {Link} from "react-router-dom"
import {useStateValue} from "./StateProvider"
import {auth} from "./firebase"

function Header() {
    const [{basket, user}, dispatch] = useStateValue()
    const handleAuthenticaton = () => {
        if (user) {
            auth.signOut()

        }
      }

  return (
    <div className='header'>
        <Link to='/'>
        <img className='header_logo' src='https://nyfta.org/wp-content/uploads/2020/09/carvel-logo-1.png' alt='carvel-logo'/>
        </Link>
        

        <div className='header_search'>
            <input className='header_searchInput' type="text"/>
            <SearchIcon className='header_searchIcon'/>
        </div>

        <div className='header_nav'>
        <Link to={!user && '/login'}>
          <div className="header_option" onClick={handleAuthenticaton} >
            <span className="header_optionLineOne">Hello Guest</span>
            <span className="header_optionLineTwo">{user ? 'Sign Out' : 'Sign In'}</span>
          </div>
        </Link>
        
            <div className='header_option'>
                <sapn className='header_optionLineOne'>Returns</sapn>
                <sapn className='header_optionLineTwo'>Orders</sapn>
            </div>
            <div className='header_option'>
                <sapn className='header_optionLineOne'>Your</sapn>
                <sapn className='header_optionLineTwo'>Prime</sapn>
            </div> 
            <Link to='/checkout'>
            <div className='header_optionBasket'>
                <ShoppingBasketIcon />
                <span className="header_optionLineTwo header_basketCount">{basket.length}</span>
            </div>
            </Link>
        </div>
    </div>
  )
}

export default Header